<template>
  <div class="order">
    <!-- 我的订单主要内容 -->
    <div class="order-content" v-if="User!==null">
      <div class="content" v-for="(item,index) in User" :key="index">
        <ul>
          <!-- 我的订单表头 -->
    
          <li class="header">
            <div class="pro-img"></div>
            <div class="pro-name">昵称</div>
            <div class="pro-price">手机号</div>
            <div class="pro-total">注册时间</div>
            <div class="pro-operate">操作</div>
          </li>
          <!-- 我的订单表头END -->

          <!-- 订单列表 -->
          <li class="product-list"  >
            <div class="pro-img">
              <router-link :to="{ path: '/goods/details', query: {productID:User[index].goodsId} }">
                <img :src="User[index].goodsCoverImg" />
              </router-link>
            </div>
            <div class="pro-name">
                {{User[index].nickName}}
              <!-- <router-link
                :to="{ path: '/goods/details', query: {productID:User[index].goodsId} }"
              >{{User[index].goodsName}}</router-link> -->
            </div>
            <div class="pro-price">{{User[index].loginName}} </div>
            <div class="pro-total">{{User[index].createTime}}</div>
           
            <div class="pro-operate">
              <el-button type="primary" size="mini"   >删除</el-button>
              <el-button type="success" size="mini"    >修改</el-button>
 
              <p v-show="User[index].orderStatus == 3">订单完成</p>
            </div>
    </li>
        </ul>
   
 
          <!-- 订单列表END -->
        </div>
      </div>
      <div style="margin-top:-40px;"></div>
    </div>
    <!-- 我的订单主要内容END -->

    <!-- 订单为空的时候显示的内容 -->
    <!-- <div v-else class="order-empty">
      <div class="empty">
        <h2>您的订单还是空的！</h2>
        <p>快去购物吧！</p>
      </div>
    </div> -->
    <!-- 订单为空的时候显示的内容END -->
  <!-- </div> -->
</template>

<script>
export default {
  data(){
    return{
      User:"",
    }

  },
    created(){
      this.getPromo()

    },
    methods:{
       getPromo( ) {
       this.$axios(   
            {
                url:  " http://localhost:3000/user", 
            //   url: this.$target+"/goodsByCategoryId", 
              method: 'get',
              params:{
               currentPage: 1,
              pageSize: 5,
              }
        })
        .then(res => {
           this.User = res.data.data;
           console.log("this.User ",this.User );
         })
        .catch(err => {
          return Promise.reject(err);
        });     
    }
  }
}
</script>

